<template>
	<div id='sysmsg' class="warp">
		<!--头部导航 start-->
		<nav class="nvs">
			<el-breadcrumb separator=">">
				<el-breadcrumb-item :to="{ path: '/message' }">消息中心</el-breadcrumb-item>
				<el-breadcrumb-item :to="{ path:'/message/systemmsg'}">系统公告</el-breadcrumb-item>
				<el-breadcrumb-item>消息详情</el-breadcrumb-item>
			</el-breadcrumb>
		</nav>
		<!--头部导航 end-->
		<!--消息内容 start-->
		<div class="msg-info">
			<div class="title">
				消息详情
			</div>
			<div class="info-warp clearfix">
				<div class="titles">
					<h3 class="tit">{{info.title}}</h3>
				</div>
				<div class="text left">
					<p class="name">发送时间</p>
					<p class="name">发送人</p>
					<p class="name">内容</p>
				</div>
				<div class="content left">
					<p class="info" v-if='send_time'>{{send_time}}</p>
					<p class="info">{{info.send_username}}</p>
					<p class="conten" v-if='info.content'>{{info.content}}</p>
				</div>
			</div>
		</div>
		<!--消息内容 end-->
	</div>
</template>

<script>
	import { getSysmsg } from '../../api';
	import { Util } from '@/config/util';
	export default {
		data() {
			return {
				info: {}, //消息数据
				send_time: null //发送时间
			}
		},
		created() {
			this.getInfo();
		},
		methods: {
			//获取数据
			getInfo() {
				let _this = this;
				let newID = this.$route.query.newsID;
				getSysmsg({
					news_info: 1,
					news_id: newID,
				}, function(resp) {
					_this.info = resp.data.info;
					let time = _this.info.send_time;
					_this.send_time = Util.TimeTo(time, 'yyyy-MM-dd hh:mm:ss');
				});
			}
		}
	};
</script>

<style lang="scss" scoped>
	@import 'src/styles/mixin.scss';
	.warp {
		background: #fff;
		.nvs {
			background: #EFF3FA;
			height: 26px;
		}
		.msg-info {
			.title {
				@include fc($sizes, $grey);
				height: 40px;
				line-height: 40px;
				background: #F6F8FC;
				text-indent: 14px;
			}
			.info-warp {
				padding-top: 40px;
					min-width:370px;
				.titles {
					.tit {
						@include fc(14px, #333);
						text-align: center;
						font-weight: bold;
					}
				}
				.text {
					@include wh(220px, 628px);
					text-align: right;
					margin-right: 14px;
					.name {
						line-height: 39px;
						@include fc(14px, #000);
						opacity: 0.57;
					}
				}
				.content {
					max-width:1200px;
					height:628px;
					.info {
						line-height: 39px;
						@include fc(14px, #333);
						height: 39px;
					}
					.conten {
						margin-top: 9px;
						@include fc(14px, #000);
					}
				}
			}
		}
	}
</style>
<style lang="scss">
	@import 'src/styles/mixin.scss';
	#sysmsg {
		.el-breadcrumb__item:last-child .el-breadcrumb__item__inner,
		.el-breadcrumb__item:last-child .el-breadcrumb__item__inner a,
		.el-breadcrumb__item:last-child .el-breadcrumb__item__inner a:hover,
		.el-breadcrumb__item:last-child .el-breadcrumb__item__inner:hover {
			color: #4877E7;
		}
	}
</style>


