import { Util } from '@/config/util';

export const getSysmsg = function(data,callback){
	Util.DataEncSubmit('news_get.php',data,(resp)=>{
		if (callback && typeof callback === "function") {
            callback(resp);
        }
	});
};

export const Savemsg = function(data,callback){
	Util.DataEncSubmit('news_save.php',data,(resp)=>{
		if (callback && typeof callback === "function") {
            callback(resp);
        }
	});
};

export const Delmsg = function(data,callback){
	Util.DataEncSubmit('news_save.php',data,(resp)=>{
		if (callback && typeof callback === "function") {
            callback(resp);
        }
	});
};



